import React from 'react';
import Heading from '../Heading';
import { Row } from 'antd';
import './style.css';
import GridCol from '../GridCol';
import parse from 'html-react-parser';

/**
 * @author
 * @function Project
 **/

const Project = (props) => {
  const val = 'PROJECTS';
  return (
    <div id='projects' className='projects'>
      <Heading title={val} />
      <Row
        className='experience_row'
        gutter={[16, 6]}
        align='middle'
        justify='space-around'
      >
        <GridCol
          designation1={'TITLE'}
          designation1_answer={'ONLINE SHOPPING CART'}
          designation2={'TECH-USED'}
          designation2_answer={
            'VANILLA JAVASCRIPT, NODE_JS, EXPRESS_JS, REST API, WEB SOCKETS,  EJS, MONGO_DB, HTML, CSS'
          }
          summary={parse(`Build a responsive full stack web application using NODEJS as a server and EJS for rendering views
          on front end. In this website, Admin can put their products he wants to sell and user can come and 
          buy those products. The functionalities implemented in this project are AUTHENTICATION for both admins
          and users, AUTHORIZATION which gives admins and users different authorities, PAGINATION, File uploads
          and downloads using MULTER, Tracking of the ordered products with realtime updation using WEBSOCKETS, 
          download invoice after order delivery, Email services for like signup, forgot password, etc. and RESTFUL API's. `)}
          btnInfo={'Visit Project'}
          href={'https://realtime-shopping-cart.herokuapp.com/'}
        />
        <GridCol
          designation1={'TITLE'}
          designation1_answer={'A BURGER BUILDER'}
          designation2={'TECH-USED'}
          designation2_answer={
            ' VANILLA JAVASCRIPT, REACT_JS, REDUX, FIREBASE, HTML, CSS'
          }
          summary={`Build a responsive Front End Web Application using REACTJS where customer can build and buy their own builded burger by 
          seeing its ingredients and price in a real time. It's a Single Page Application(SPA). 
          It uses REDUX for a global state management and realtime database in FIREBASE. The functionalities in 
          this projects are AUTHENTICATION by a token received from Firebase (like a JWT token), record of previous 
          ORDERS, State Management using REDUX, making API'S requests ( get, post,etc.. ). Learned how to work with both CLASS and FUNCTIONAL components with lifecycle HOOKS in React. `}
          btnInfo={'Visit Project'}
          href={'https://react-my-burger-50b9e.web.app/'}
        />
        <GridCol
          designation1={'TITLE'}
          designation1_answer={'A JavaScript Game'}
          designation2={'TECH-USED'}
          designation2_answer={'HTML, CSS, JavaScript, DOM'}
          summary={`Build a responsive JavaScript Game with using some front end technologies. The main 
          motive to build this project is to learn how to manipulate the HTML elements using Document Object Model(DOM) and to got an excellent command in HTML, CSS, JavaScript. `}
          btnInfo={'Visit Project'}
          href={'https://aakashmehtaa1308.github.io/javascript-game/'}
        />
      </Row>
    </div>
  );
};

export default Project;
