import React from 'react';
import { Row, Col, Typography, Button, Divider } from 'antd';
import './style.css';

import { ManOutlined } from '@ant-design/icons';
import Heading from '../Heading';
import GridCol from '../GridCol';

const { Title, Paragraph } = Typography;
/**
 * @author
 * @function Experience
 **/

const Experience = (props) => {
  const val = 'EXPERIENCE';
  return (
    <div id='experience' className='experience'>
      <Heading title={val} />

      <Row
        className='experience_row'
        gutter={[16, 16]}
        align='middle'
        justify='center'
      >
        <GridCol
          designation1={'POSITION'}
          designation1_answer={'Front End Web Developer'}
          designation2={'COMPANY'}
          designation2_answer={'Vkarma Edutech Private Limited'}
          summary={`Did an Internship as a Front End Web Developer in a company named VKARMA Edutech Private
          Limited for 2 months started from 11 May, 2020. In this Internship, I built WEB PAGES for the company's 
          website and handle many other responsibilties. Apart from Technical side, I also deal with how to work 
          and coordinate with a team in a project, completing tasks with in a deadline, Attended daily virtual 
          meetings for discussion and updates. After doing this internship, I was fully confident about my front 
          end skills and my ability to design as many complex web pages. `}
          btnInfo={'Visit Vkarma'}
          href={'https://vkarma.in/'}
        />
      </Row>
    </div>
  );
};

export default Experience;
